.taxDeduction {
    background: #FFFFFF;
    border-radius: 30px;
    padding: 32px;
    position: relative;
    width: calc(552px - 32px - 32px);
    display: flex;
    flex-direction: column;
}

.closeBtn {
    position: absolute;
    top: 27px;
    right: 27px;
}

.taxDeduction h3 {
    font-style: normal;
    font-weight: bold;
    font-size: 28px;
    line-height: 40px;
    color: #000000;
    margin: 0;
}

.taxDeduction p {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: #808080;
}

.checkboxBlocksContainer {
    display: flex;
    flex-direction: column;
    max-height: 150px;
    overflow-y: auto;
}

.checkboxBlocksHeader {
    display: flex;
    justify-content: space-between;
}

.taxDeduction span {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 24px;
    color: #000000;
}

.tagContainer {
    display: flex;
    align-items: center;
    padding: 20px 0;
    margin-bottom: 10px;
}

.tagContainer > button {
    margin-left: 15px;
}

.addButtonContainer {
    width: 100%;
}

.addButtonContainer > button {
    width: 100%;
}

.addButtonContainer_checkboxBlock_empty {
    width: 100%;
}

.addButtonContainer_checkboxBlock_empty > button {
    width: 100%;
}

@media screen and (max-width: 768px) {
    .taxDeduction {
        width: calc(453px - 32px - 32px);
    }
}

@media screen and (max-width: 568px) {
    .taxDeduction {
        border-radius: 0;
        width: calc(100vw - 32px - 32px);
        height: calc(100vh - 32px - 32px);
        overflow-y: auto;
    }
    .checkboxBlocksContainer {
        max-height: max-content;
        overflow-y: initial;
    }
    .addButtonContainer_checkboxBlock_empty {
        position: absolute;
        bottom: 32px;
        width: calc(100vw - 32px - 32px);
    }
}