.button {
    padding: 16px 0;
    min-width: 280px;
    /*width: 280px;*/
    font-size: 16px;
    height: max-content;

    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    box-shadow: 0 0 24px rgba(234, 0, 41, 0.33);
    color: #FFFFFF;
    border-radius: 6px;

    justify-content: center;
    align-items: center;
    transition: all 250ms;
    border: none;
    cursor: pointer;
}

.button:hover {
    background: #EA0029;
}

.button.border {
    border: 1px solid #FFFFFF;
}

.button.border:hover {
    background: #FFFFFF;
    color: black;
}

.button.disabled {
    background: #BEC5CC!important;
    cursor: default!important;
    box-shadow: none;
}

.button.disabled:hover {
    background: #BEC5CC!important;
    cursor: default!important;
}

.button.text {
    background: none;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: #EA0029;
    box-shadow: none;
    width: max-content;
    height: max-content;
    padding: 3px 0;
    min-width: max-content;
}

.button.text:hover {
    color: #F53A31;

}

.button.small {
    padding: 12px 0;
    font-size: 12px;
}

.button.tag {
    width: max-content;
    display: flex;
    align-items: center;
    padding: 6px 12px;
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    border-radius: 50px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: #FFFFFF;
    box-shadow: none;
    min-width: max-content;
    cursor: default;
}

.button.tag.notActive {
    background: #EEF0F2;
    color: #000000;
    cursor: pointer;
}

.button.tag.notActive:hover {
    color: #000000;
    background: #DFE3E6;
}

.button.fullScreenWidth {
    width: 100%!important;
}