.checkboxContainer {
    display: flex;
    align-items: center;
}

.checkbox {
    height: 20px;
    width: 20px;
    background: #FFFFFF;
    border: 1px solid #DFE3E6;
    box-sizing: border-box;
    border-radius: 6px;
    transition: all 250ms;
    cursor: pointer;
}

.checkbox:hover {
    border-color: #000000;
}

.checkbox.disabled {
    background: #BEC5CC;
    border-color: #BEC5CC;
    cursor: default;
}

.checkbox.checked {
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    display: flex;
    justify-content: center;
    align-items: center;
}

.checkbox.checked.disabled {
    background: #BEC5CC;
}

.checkbox.checked.disabled:hover {
    background: #BEC5CC;
}

.checkbox.checked image {
    width: 14px;
    height: 11px;
}

.checkboxLabel {
    margin-left: 11px!important;
    color: black!important;
    font-weight: bold!important;
}