.input {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.inputWrapper {
    position: relative;
    width: 100%;
}

.prefix {
    position: absolute;
    top: 12px;
    left: 7px;
    color: #BEC5CC;
    font-weight: bold;
}

.inputWrapper input {
    border: 1px solid #DFE3E6;
    box-sizing: border-box;
    border-radius: 3px;
    outline: none;
    width: 100%;
    font-size: 16px;
    padding: 12px 25px;
    transition: all 150ms;
}

.inputWrapper input:hover {
    border-color: #000000;
}

.inputWrapper input::placeholder {
    color: #BEC5CC;
}

.inputWrapper input:disabled {
    border-color: #808080;
}

.label {
    margin-bottom: 20px!important;
    font-size: 14px!important;
    color: #000000!important;
    font-weight: bold!important;
}

.input span {
    height: 12px;
}

.input.error input {
    border-color: #EA0029;
}

.errors {
    min-height: 12px;
    display: flex;
    flex-direction: column;
    padding-bottom: 4px;
}

.errors span {
    font-size: 10px;
    color: #EA0029;
}